/* Editable */

#preloader {
  background-color: #fff;
  padding: 0;
  margin: 0;
  height: 100%;
  position: fixed;
  z-index: 99999;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  .lds-ripple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-ripple div {
    position: absolute;
    border: 4px solid var(--primary);
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  .lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
  }
  @keyframes lds-ripple {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
}

[data-theme-version="dark"] {
  #preloader {
    background-color: $d-bg;
  }
}

.flash-screen {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #e5f1fd;

  @keyframes rotate {
    100% {
      transform: rotate(1turn);
    }
  }

  .flash-body {
    position: relative;
    z-index: 0;
    width: 400px;
    height: 200px;
    border-radius: 10px;
    overflow: hidden;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: sans-serif;
    font-weight: bold;

    &::before {
      content: "";
      position: absolute;
      z-index: -2;
      left: -50%;
      top: -50%;
      width: 200%;
      height: 260%;
      background-color: #e58c3c;
      background-repeat: no-repeat;
      background-size: 50% 50%, 50% 50%;
      background-position: 0 0, 100% 0, 100% 100%, 0 100%;
      //   background-image: linear-gradient(#399953, #399953),
      //     linear-gradient(#fbb300, #fbb300), linear-gradient(#d53e33, #d53e33),
      //     linear-gradient(#377af5, #377af5);

      background-image: linear-gradient(#004aad, #377af5),
        linear-gradient(#fbb300, #e58c3c);
      animation: rotate 2s linear infinite;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: -1;
      left: 6px;
      top: 6px;
      width: calc(100% - 12px);
      height: calc(100% - 12px);
      background: #e5f1fd;
      border-radius: 5px;
    }
  }
}
